import React, { useEffect } from "react";
import "./styles.css";
import { connect } from 'react-redux'

import GameBoy from "./components/GameBoy";
import PokeList from "./components/PokeList";
import fetchPokemons from './store/fetchPokemons';
import Loader from './components/Loader'

import { showPokemonAction, catchPokemonAction, changeGameBoyColor } from './store/action'

const App = ({ 
    fetchPokemons, 
    pending, 
    nbPokemonsACharger, 
    handleClickA,
    handleClickB,
    handleClickStart,
    handleClickSelect,
    handleColorChange,
    pokemons,
    onScreen,
    color}) => {
  useEffect( () => {
    fetchPokemons(nbPokemonsACharger)
    }, [fetchPokemons, nbPokemonsACharger]
  )

  if (pending) {
    return <Loader />
  }

  const couleurs = ['bleu', 'jaune']

  const radioGroupCouleurs = couleurs.map(couleur => (
    <div key={couleur} className='bloc-couleur'>
      <input 
        type='radio' 
        id={couleur}
        name='color' 
        value={couleur}
        onChange={() => handleColorChange(couleur)}
        checked={couleur === color}
        />
      <label htmlFor={couleur}>{couleur}</label>
    </div>
  ))

  return (
    <div className="App">
    {/**
      <RadioNodeL label=''
      <div className='choix-couleur'>
        <input 
          type='radio' 
          id='bleu'
          name='color' 
          value='bleu'
          onChange={this.handleChangeColor}
          />
        <label for='bleu'>Bleu</label>
        <input 
          type='radio' 
          id='jaune' 
          name='color' 
          value='jaune'
          onChange={this.handleChangeColor}
          />
        <label for='jaune'>Jaune</label>
      </div>  
    */}
      <div className='choix-couleurs'>
        <h5>Couleur GB</h5>
        {radioGroupCouleurs}
      </div>
      <GameBoy
        handleClickA={() => handleClickA(onScreen)}
        handleClickB={() => handleClickB(pokemons)}
        handleClickStart={() => handleClickStart(pokemons)}
        handleClickSelect={() => handleClickSelect(pokemons)}
        color={color}
        />
      <PokeList />
    </div>
  );
};

const mapStateToProps = ({ nbPokemonsACharger, pending, pokemons, onScreen, color }) => {
  return {
    nbPokemonsACharger,
    pending,
    pokemons,
    onScreen,
    color
  }
}

const mapDispatchToProps = dispatch => {
  const showPokemon = pokemons => dispatch(showPokemonAction(pokemons))
  return {
    handleClickA: pokemon => dispatch(catchPokemonAction(pokemon)),
    handleClickB: showPokemon,
    handleClickStart: showPokemon,
    handleClickSelect: showPokemon,
    handleColorChange: color => dispatch(changeGameBoyColor(color)),
    fetchPokemons: nbPokemons => dispatch(fetchPokemons(nbPokemons))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
